@mixin accessibility_onetap_admin_menu_controls() {
	// Box controls custom 
	.custom-box-controls {
		margin-bottom: 20px;

		label {
			display: block;
			font-size: 14px;
			font-weight: 500;
			margin-bottom: 8px;
			color: $label-color;
		}

		input,
		select {
			border: 1px solid $border-color;
			border-radius: 8px 8px 8px 8px;
			padding: 10px 13px;
			width: 100%;
			height: 43px;
		}
	}

	// Toggle
	.box-control-toggle {

		.switch {
			position: relative;
			display: flex;
			flex-wrap: wrap;
			align-items: center;
			height: 23px;
			gap: 15px;

			input {
				display: none;
			}
		}

		.slider {
			position: absolute;
			cursor: pointer;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			background: $border-color;
			-webkit-transition: 0.4s;
			transition: 0.4s;
			margin: 0;
		}

		.slider::before {
			content: "";
			position: absolute;
			height: 17px;
			width: 18px;
			left: 3px;
			bottom: 3px;
			background: $light-bg;
			-webkit-transition: 0.4s;
			transition: 0.4s;
		}

		input:checked + .slider {
			background: $primary-color;
		}

		input:focus + .slider {
			box-shadow: $primary-color;
		}

		input:checked + .slider::before {
			-webkit-transform: translateX(22px);
			-ms-transform: translateX(22px);
			transform: translateX(22px);
		}

		.slider.round {
			width: 46px;
			border-radius: 34px;
		}

		.slider.round::before {
			border-radius: 50%;
		}

		.slider-red.round::before {
			border-radius: 50%;
		}

		span.label {
			position: relative;
			left: 60px;
			font-size: 14px;
			font-weight: 500;
			color: $dark-color;
			margin: 0;
			display: block;
			cursor: pointer;
		}
	}

	// Divider
	.divider {
		margin: 20px 0;
		border-style: solid;
		border-width: 0 0 1px 0;
		border-color: $border-color;
	}

	// Dropdown
	.dropdown-box-control {

		select {
			padding: 5px 14px;
		}
	}

	// Checkbox
	.checkbox-box-control {

		input {
			margin: 6px;
			margin-left: 0;
			border: 1px solid #ddd;
			border-radius: 3px;
			width: 16px;
			height: 16px;
		}

		label {
			font-weight: 400;
			font-size: 16px;
			line-height: 1.6;
		}
	}

	// Other settings
	.box-control-other-settings {
		background: $light-bg;
		position: relative;
		border-radius: 0 0 1rem 1rem;
		padding: 2rem;
		padding-top: 0;

		.box-other-settings {
			display: flex;
			flex-wrap: wrap;
			align-items: center;
			justify-content: space-between;
			padding-bottom: 2rem;

			.left {

				span.title {
					font-size: 1rem;
					font-weight: 500;
					color: $dark-color;
					margin: 0;
					display: block;
				}
			}

			.right {

				.code {
					background: $second-light-color;
					padding: 12px;
					border-radius: 8px;
				}
			}
		}
	}

	// Device position
	.box-device {

		.boxes {
			display: flex;
			flex-wrap: wrap;
			gap: 14px;

			button {
				display: flex;
				flex-wrap: wrap;
				align-items: center;
				gap: 5px;
				background: $light-bg;
				border-radius: 8px;
				padding: 12px 16px;
				border: 1px solid $border-color;
				line-height: 20px;
				cursor: pointer;

				img {
					width: 18px;
					height: 18px;
					opacity: 0.6;
				}

				span {
					display: flex;
					align-items: center;
					font-size: 14px;
					font-weight: 600;
					text-align: center;
					line-height: 20px;
					margin: 0;
					color: $divider-text;
				}
			}

			button.active {
				border: 1px solid transparent;
				background: #eee;

				img {
					opacity: 1;
				}

				span {
					color: $dark-color;
				}
			}
		}
	}	

	// Responsive other settings
	@media only screen and (max-width: 576px) {

		.box-control-other-settings {

			.box-other-settings {
				gap: 25px;
			}
		}
	}	

}

