					&::after {
						content: "";
						width: calc(100% + 64px);
						height: 1px;
						background: $border-color;
						position: absolute;
					}

					&::before {
						top: 0;
					}

					&::after {
						bottom: 0;
					}

					li {
						margin: 0;
						padding: 0;
						list-style: none;

						a {
							height: 41px;
