	body.admin_page_onetap-module-labels {

		.wrap {

			.box-custom-checkbox {
				display: flex;
				flex-wrap: wrap;
				align-items: center;
				justify-content: space-between;
				background: $light-bg;
				border-bottom: 1px solid #eee;

				.left {
					display: flex;
					flex-wrap: wrap;
					padding: 32px;
					width: calc(100% - 150px);

					.icon {
						font-size: 30px;
						padding: 10px;
						border-radius: 8px 8px 8px 8px;
						background: #eff6ff;
						fill: $primary-color;
						color: $primary-color;
						width: 30px;
						height: 30px;
						margin-right: 15px;
					}

					.text {
						width: calc(100% - 65px);
						display: flex;
						flex-wrap: wrap;
						align-items: center;

						span.title {
							font-size: 1rem;
							font-weight: 500;
							color: $dark-color;
							margin: 0;
							display: block;
						}

						span.desc {
							font-weight: 400;
							margin: 0;
							display: none;
							margin-top: 7px;
						}
					}
				}

				.right {
					position: relative;
					right: 32px;

					.switch {
						position: relative;
						display: inline-block;
						width: 46px;
						height: 23px;

						input {
							display: none;
						}
					}

					.slider {
						position: absolute;
						cursor: pointer;
						top: 0;
						left: 0;
						right: 0;
						bottom: 0;
						background: $border-color;
						-webkit-transition: 0.4s;
						transition: 0.4s;
						margin: 0;
					}

					.slider::before {
						content: "";
						position: absolute;
						height: 17px;
						width: 18px;
						left: 3px;
						bottom: 3px;
						background: $light-bg;
						-webkit-transition: 0.4s;
						transition: 0.4s;
					}

					input:checked + .slider {
						background: $primary-color;
					}

					input:focus + .slider {
						box-shadow: $primary-color;
					}

					input:checked + .slider::before {
						-webkit-transform: translateX(22px);
						-ms-transform: translateX(22px);
						transform: translateX(22px);
					}

					.slider.round {
						border-radius: 34px;
					}

					.slider.round::before {
						border-radius: 50%;
					}

					.slider-red.round::before {
						border-radius: 50%;
					}
				}

				&.onetap-pro {
					opacity: 0.6;
					filter: grayscale(1);

					.left {

						.text {
							position: relative;

							span.status-pro {
								font-size: 0.75rem;
								padding: 0.25rem 0.5rem;
								color: $light-bg;
								background: $dark-color;
								border-radius: 1rem;
								font-weight: 500;
								position: absolute;
							}

							span.title {
								position: relative;
								left: 45px;
							}
						}
					}

					.right {
						position: relative;

						&::before {
							content: "";
							position: absolute;
							top: 0;
							left: 0;
							width: 100%;
							height: 100%;
							z-index: 9999999999999999;
						}
					}

				}
			}

			.metabox-holder {

				>div {
					display: none !important;
				}

				>div:nth-child(3) {
					display: block !important;
				}

				table {

					tr {

						td {
							padding-top: 0;
							padding-bottom: 0;
						}
					}

					tr:nth-child(2),
					tr:nth-child(13),
					tr:nth-child(20) {

						td {

							.box-custom-input-text {
								border-radius: 1rem 1rem 0 0;
							}
						}
					}

					tr:nth-child(12),
					tr:nth-child(19),
					tr:nth-child(30) {

						td {

							.box-custom-input-text {
								border-radius: 0 0 1rem 1rem;
							}
						}
					}

					tr:last-child {

						td {

							.box-custom-checkbox {
								border-radius: 0 0 1rem 1rem;

								.left {

									.icon {

										button {
											display: flex;
											min-width: 113px;
											position: relative;
											right: 44px;
											top: 50px;
										}
									}
								}
							}
						}
					}

					tr.accessibility-profiles-title {

						td {
							padding-bottom: 0;

							.box-setting-option.icons,
							.box-setting-option.size,
							.box-setting-option.border {

								.box-control {
									padding-top: 0;
									border-radius: 0;

									.title-label {
										font-size: 0.925rem;
										font-weight: 500;
										line-height: 28px;
										color: $label-color;
										display: block;
										cursor: pointer;
										margin-bottom: 0.5rem;
										width: 100%;
									}

									.boxes {
										display: flex;
										flex-wrap: wrap;
										justify-content: flex-start;
										gap: 20px;

										.box {

											label {
												border: 1px solid $border-color;
												border-radius: 8px;
												margin: 0;
												line-height: normal;
												height: 135px;
												width: 135px;
												display: flex;
												align-items: center;
												justify-content: center;

												&.checked {
													border: 1px solid $primary-color;
													box-shadow: 0 0 0 1px $primary-color;
												}

												img {
													height: 30px;
													width: 30px;
													padding: 15px;
													background: $primary-color;
													border-radius: 50%;
												}

												input {
													display: none;
												}
											}
										}
									}

									button.save-changes {
										display: none;
									}
								}
							}

							// Icons
							.box-setting-option.icons {

								.box-control {
									padding-top: 2rem;
									border-radius: 1rem 1rem 0 0;
								}
							}

							// Size
							.box-setting-option.size {

								.site-description {
									margin: 0;
								}

								.box-control {

									.boxes {

										.box:nth-child(1) {

											img {
												height: 20px;
												width: 20px;
												padding: 10px;
											}
										}

										.box:nth-child(2) {

											img {
												height: 30px;
												width: 30px;
												padding: 15px;
											}
										}

										.box:nth-child(3) {

											img {
												height: 35px;
												width: 35px;
												padding: 17.5px;
											}
										}
									}
								}
							}

							// Border
							.box-setting-option.border {

								.site-description {
									margin: 0;
								}

								.box-control {
									border-radius: 0 0 1rem 1rem;

									.boxes {

										.box:nth-child(1) {

											img {
												border: solid 2px $light-color;
												box-shadow: 0 0 0 4px $primary-color;
											}
										}
									}

									button.save-changes {
										display: block;
									}
								}
							}
						}
					}

					tr.accessibility-profiles {

						td {

							.box-custom-checkbox {
								border-radius: 1rem;
								margin-bottom: 64px;
								border: none;
							}
						}
					}

					tr.content-modules-title-save-changes,
					tr.color-modules-save-changes,
					tr.orientation-modules-save-changes {

						td {

							button {
								margin-top: 20px;
								margin-bottom: 64px;
							}
						}
					}
				}
			}

			header.top {
				position: relative;

				&::before {
					content: "";
					position: absolute;
					background: $light-bg;
					width: calc(100% + 64px);
					left: -32px;
					top: -20px;
					height: calc(100% + 50px);
				}
			}			
		}
	}

	body.onetap_page_accessibility-onetap-accessibility-status {

		.wrap {

			header.top {
				position: relative;

				&::before {
					content: "";
					position: absolute;
					background: $light-bg;
					width: calc(100% + 64px);
					left: -32px;
					top: -20px;
					height: calc(100% + 50px);
				}
			}

			// Hide the submit button by default
			p.submit {
				display: none;
			}

			form.accessibility-status {
				position: relative;
				bottom: -10px;
			}
			
			.accessibility-status-container {
				display: flex;
				flex-wrap: wrap;
				justify-content: space-between;
				gap: 50px;
				background: $light-bg;
				padding: 32px;
				border-radius: 16px;

				.left-column {
					width: 271px;

					.box-control-toggle {

						span {
							margin: 0;
							font-size: 14px;
							color: $dark-color;
						}
					}

					button.generate-accessibility-statement {
						width: 100%;
						margin: 0;
						opacity: 0.7;

						&.active {
							opacity: 1;
						}
					}
				}

				.right-column {
					width: calc(100% - 271px - 50px);

					// Readmore
					.readmore {
						display: flex;
						align-items: center;
						padding: 16px;
						background-color: #FEFCE8;
						border-radius: 12px;
						margin-bottom: 20px;

						svg {
							width: 22px;
							height: 22px;
							margin-right: 10px;
							fill: #FDC700;
						}

						span {
							font-size: 14px;
							line-height: 20px;
							color: #894B00;
							transition: color 0.3s;
							display: inline-block;
							margin: 0;

							a {
								font-weight: 600;
								color: #894B00;
							}
						}
					}

					// Editor
					.editor {
						position: relative;

						.wp-editor-container {
							border: none;
							border-radius: 16px;
							box-shadow: none;

							.mce-tinymce {
								background: #F9F9F9;
								border-radius: 16px;
								border: 1px solid $border-color;

								.mce-top-part.mce-stack-layout-item {
					
									&::before {
										box-shadow: none;
									}

									.mce-toolbar-grp {
										background: none;
										border: none;
										padding: 4px 8px;
									}
								}

								.mce-statusbar {
									border-radius: 0 0 16px 16px;
									padding: 5px;
									border: none;
								}
							}
						}

						.copy {
							display: flex;
							flex-wrap: wrap;
							align-items: center;
							color: $dark-color;
							position: absolute;
							right: 20px;
							top: 15px;
							margin: 0;
							font-size: 12px;
							cursor: pointer;
							font-weight: 500;
							transition: color 0.3s ease;
							opacity: 0;
							visibility: hidden;
							animation: showCopy 0.3s ease-in-out forwards;
							animation-delay: 1.3s;

							svg {
								width: 12px;
								margin-right: 5px;
							}

							&.copy-text {
								transition: all 0.3s ease;
							}
						}

						@keyframes showCopy {

							to {
								opacity: 1;
								visibility: visible;
							}
						}					
					}
				}
			}

			@media only screen and (max-width: 1200px) {

				.accessibility-status-container {

					.left-column,
					.right-column {
						width: 100%;

						select {
							max-width: 100%;
						}
					}
				}
			}

			@media only screen and (max-width: 782px) {

				form.accessibility-status {
					bottom: 0;
				}

				.accessibility-status-container {
					gap: 20px;
					padding: 20px;

					.right-column {

						.readmore {

							svg {
								width: 55px;
								height: 55px;
							}
						}
					
						.editor {

							.copy {
								display: none;
							}
						}
					}
				}
			}
		}
	}	

